import React from 'react';
import './Skills.css';

const skills = [
  { name: 'HTML', icon: 'https://cdn-icons-png.flaticon.com/512/732/732212.png', link: 'https://developer.mozilla.org/en-US/docs/Web/HTML' },
  { name: 'CSS', icon: 'https://cdn-icons-png.flaticon.com/512/732/732190.png', link: 'https://developer.mozilla.org/en-US/docs/Web/CSS' },
  { name: 'Bootstrap', icon: 'https://cdn-icons-png.flaticon.com/512/5968/5968672.png', link: 'https://getbootstrap.com/' },
  { name: 'JavaScript', icon: 'https://cdn-icons-png.flaticon.com/512/5968/5968292.png', link: 'https://developer.mozilla.org/en-US/docs/Web/JavaScript' },
  { name: 'SQL', icon: 'https://cdn-icons-png.flaticon.com/512/4299/4299956.png', link: 'https://www.w3schools.com/sql/' },
  { name: 'C', icon: 'https://cdn-icons-png.flaticon.com/512/6132/6132221.png', link: 'https://www.programiz.com/c-programming' },
  { name: 'C++', icon: 'https://cdn-icons-png.flaticon.com/512/6132/6132222.png', link: 'https://www.programiz.com/cpp-programming' },
  { name: 'MS Office', icon: 'https://cdn-icons-png.flaticon.com/512/732/732221.png', link: 'https://www.microsoft.com/en/microsoft-365/office' },
  { name: 'MS Excel', icon: 'https://cdn-icons-png.flaticon.com/512/732/732220.png', link: 'https://support.microsoft.com/en-us/excel' },
  { name: 'React', icon: 'https://cdn-icons-png.flaticon.com/512/1126/1126012.png', link: 'https://reactjs.org/' },
  { name: 'Spring Boot', icon: 'https://cdn-icons-png.flaticon.com/512/919/919836.png', link: 'https://spring.io/projects/spring-boot' },
  { name: 'GitHub', icon: 'https://cdn-icons-png.flaticon.com/512/733/733553.png', link: 'https://github.com/' },
  { name: 'MySQL', icon: 'https://cdn-icons-png.flaticon.com/512/528/528260.png', link: 'https://www.mysql.com/' },
  { name: 'VS Code', icon: 'https://cdn-icons-png.flaticon.com/512/906/906324.png', link: 'https://code.visualstudio.com/' },
  { name: 'Postman', icon: 'https://cdn.iconscout.com/icon/free/png-256/postman-3521648-2945092.png', link: 'https://www.postman.com/' },
  { name: 'IntelliJ IDEA', icon: 'https://resources.jetbrains.com/storage/products/intellij-idea/img/meta/intellij-idea_logo_300x300.png', link: 'https://www.jetbrains.com/idea/' },
  { name: 'Netlify', icon: 'https://seeklogo.com/images/N/netlify-logo-758722CDF4-seeklogo.com.png', link: 'https://www.netlify.com/' },
  { name: 'Vercel', icon: 'https://assets.vercel.com/image/upload/front/favicon/vercel/favicon.ico', link: 'https://vercel.com/' }
];

const Skills = () => {
  return (
    <div className="skills-section">
      <h2>Skills</h2>
      <div className="skills-grid">
        {skills.map((skill, index) => (
          <a
            className="skill-card"
            key={index}
            href={skill.link}
            target="_blank"
            rel="noopener noreferrer"
          >
            <img src={skill.icon} alt={skill.name} />
            <span>{skill.name}</span>
          </a>
        ))}
      </div>
    </div>
  );
};

export default Skills;
